<template>
  <div class="properties">
    <h2>Properties</h2>
    <pre>{{ getProperties() }}</pre>
  </div>
</template>

<script>
export default {
  name: 'Properties',
  props: ['data'],
  methods: {
    getProperties() {
      const data = this.data;

      try {
        const body = JSON.parse((typeof data !== 'string') ? JSON.stringify(data) : data);
        if (body) {
          delete body._links;
          delete body._embedded;
        }
        return body;
      } catch (e) {
        return '';
      }
    }
  }
}
</script>

<style scoped>
  .properties {
    width: 100%;
  }
</style>
